import React from 'react';
const bcrypt = require('bcryptjs');

export default class BryptForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = { password: '', saltRounds: '', hash: '' };

        this.handleChangePassword = this.handleChangePassword.bind(this);
        this.handleChangeSaltRounds = this.handleChangeSaltRounds.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleChangePassword(event) {
        this.setState({ password: event.target.value });
    }

    handleChangeSaltRounds(event) {
        this.setState({ saltRounds: event.target.value });
    }

    handleSubmit(event) {
        //alert('Generating with password ' + this.state.password + ' and ' + this.state.saltRounds + ' salt rounds');
        var salt = bcrypt.genSaltSync(Number(this.state.saltRounds));
        var hash = bcrypt.hashSync(this.state.password, salt);
        //alert('Hash is ' + hash);
        event.preventDefault();
        this.setState({ hash: hash });
    }

    render() {
        return (
            <div className="FormContainer">
                <form className="Form" onSubmit={this.handleSubmit}>
                    <div className="FormTitle">
                        Bcrypt
                    </div>
                    <label>
                        Password
                        <input type="text" value={this.state.password} onChange={this.handleChangePassword} />
                    </label>
                    <label>
                        Salt Rounds
                        <input type="text" value={this.state.saltRounds} onChange={this.handleChangeSaltRounds} />
                    </label>
                    <input type="submit" value="Generate" />
                    <div>
                        <textarea type="text" value={this.state.hash} />
                    </div>
                </form>
            </div>
        );
    }
}